<template>
  <div class="py-5">
    <div class="container">
      <b-card
        :title="imageInfo.title"
        :sub-title="imageInfo.city+' '+imageInfo.country"
        :img-src="imageInfo.largePath"
        img-top
        class="mb-2"
      >
        <b-card-text>{{imageInfo.description}}</b-card-text>
        <b-button variant="outline-success">Like</b-button>
      </b-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      id: this.$route.params.id,
      imageInfo: {}
    };
  },
  created() {
    this.getImageById();
  },
  methods: {
    getImageById() {
      axios
        .get("../api/GetImageById.php", {
          params: {
            id: this.id
          }
        })
        .then(response => {
          console.log(this.id);
          console.log(response);
          this.imageInfo = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

