<template>
  <div class="py-5">
    <div class="container">
      <b-card-group columns>
        <b-card
          :title="image.title"
          :img-src="image.largePath"
          img-top
          tag="article"
          class="mb-4 shadow-sm"
          align-self="stretch"
          v-for="(image,index) in popularImages"
          :key="index"
        >
          <b-card-text class="brief">{{image.description}}</b-card-text>
          <b-button-group>
            <b-button variant="outline-primary" :to="'/detail/'+image.id">View</b-button>
            <b-button
              variant="outline-success"
              :active="isFavor(image.id)"
              @click="like(image.id)"
            >{{image.favorCount}} Like</b-button>
          </b-button-group>
        </b-card>
      </b-card-group>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      popularImages: {}
    };
  },
  computed: {
    favorList() {
      return this.$store.getters.getFavorList;
    }
  },
  created() {
    this.getPopularImage();
  },
  methods: {
    getPopularImage() {
      axios
        .get("../api/GetPopularImage.php")
        .then(response => {
          this.popularImages = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    isFavor(id) {
      return this.favorList.includes(id + "");
    },
    like(imageid) {
      if (this.$store.getters.isLogin) {
        this.$store.dispatch("like", { imageid: imageid });
      } else {
        this.$route.push({ name: "login" });
      }
    }
  }
};
</script>

<style>
.brief {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
</style>